.Splash {
  margin-top: 20%;
  -webkit-animation-name: animate-splash;
          animation-name: animate-splash;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-timing-function: cubic-bezier(0.64, 0.18, 0, 1);
          animation-timing-function: cubic-bezier(0.64, 0.18, 0, 1);
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes animate-splash {
  to {
    margin-top: 30px;
  }
}

@keyframes animate-splash {
  to {
    margin-top: 30px;
  }
}



