.CategorySelector {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  text-transform: capitalize;
  -ms-flex-pack: center;
      justify-content: center;
}

.CategorySelector li {
  cursor: pointer;
  padding: 0 10px;
  border-right: 1px solid black;
}

.CategorySelector li:last-of-type {
  border: none;
}

.CategorySelector .active {
  font-weight: bold;
}

.CategorySelector.animate {
  opacity: 0;
  -webkit-animation-name: animate-category-selector;
          animation-name: animate-category-selector;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes animate-category-selector {
  to {
    opacity: 1;
  }
}

@keyframes animate-category-selector {
  to {
    opacity: 1;
  }
}



